/* src/index.css */

/* Global styles */
body {
  font-family: 'Arial', sans-serif;
  margin: 0;
  padding: 0;
}

.container {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
}

a {
  color: #007bff;
  text-decoration: none;
  cursor: pointer;
}

a:hover {
  text-decoration: underline;
}

/* Header styles */
/* Header styles */
header {
  background-color: #333;
  color: white;
  padding: 20px; /* Increased padding for a better appearance */
  text-align: center;
  position: relative;
}

header h1 {
  margin: 0;
}

.cover-photo {
  background: url('https://source.unsplash.com/1600x400/?dog') center/cover no-repeat;
  height: 400px; /* Adjust the height as needed */
  display: flex;
  align-items: center;
  justify-content: center;
}

/* Calendar styles */
.calendar-container {
  margin-top: 20px;
}

/* Signup form styles */
.signup-form {
  margin-top: 20px;
}

.signup-form label {
  display: block;
  margin-bottom: 10px;
}

.signup-form input,
.signup-form select {
  width: 100%;
  padding: 10px;
  margin-bottom: 15px;
}

.signup-form button {
  background-color: #007bff;
  color: white;
  padding: 10px 20px;
  cursor: pointer;
}
