/* src/components/Header.css */
.header {
    background: center/cover no-repeat;
    color: #fff;
    padding: 1rem;
    text-align: center;
    min-height: 350px; /* Adjust the minimum height as needed */
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    object-fit: cover; /* Ensure the background image maintains its aspect ratio */
  }
  
  nav {
    margin-top: 1rem;
  }
  
  ul {
    list-style: none;
    padding: 0;
    margin: 0;
  }
  
  li {
    display: inline-block;
    margin-right: 1rem;
  }
  
  a {
    text-decoration: none;
    color: #fff;
    font-weight: bold;
  }
  